.YoutubeChannel {
    padding: .7rem;
    background-color: white;
    border: 1px solid rgb(var(--baseClr));
    border-radius: .2rem;
    width: 30rem;
    max-width: 95vw;
    margin-bottom: 1rem;
    cursor: pointer;
    display: flex;
    align-items: center;
}

.YoutubeChannel:hover {
    background-color: rgba(var(--baseClr), .1);
}

.CheckBox {
    width: .7rem;
    height: .7rem;
    margin-right: .7rem;
    border: 1px solid rgb(var(--baseClr));
    border-radius: 50%;
}

.Selected {
    background-color: rgb(var(--baseClr));
}

.YoutubeChannel > p {
    line-height: .9;
}