.App {
    min-height: 100vh;
    background-color: rgb(var(--backClr));
    height: 100%;  
    padding-top: 7rem;
}

.SearchButton {
    display: block;
    margin: 2rem auto;
    font-size: 1rem;
    border-radius: .2rem;
    padding: .2rem 2rem;
    cursor: pointer;
}

.ClipsContainer {
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 60rem;
    margin: 0 auto;
}

@media (max-width: 960px) {
    .ClipsContainer {
        flex-direction: column;
        max-width: 95vw;
    }
}