.Clip {
    padding: .2rem 2.4rem .2rem .2rem;
    border: 1px solid rgba(var(--baseClr), .5);
    border-radius: .2rem; 
    margin-bottom: .6rem; 
    position: relative;
    cursor: pointer;
}

.ClipContent:hover .ClipTitle {
    text-decoration: underline;
}

.ClipTitle {
    font-size: .9rem;
}

.ClipDate {
    font-size: .65rem;
}

.HideClipButton {
    height: 1.6rem;
    width: 1.6rem;
    background: url('../assets/icons/hide.svg') center center / 80% no-repeat;
    border-radius: .2rem;
    position: absolute;
    right: .5rem;
    top: calc(50% - .8rem);
    z-index: 9;
}

.HideClipButton:hover {
    background-color: rgba(var(--baseClr), .2);
}